.app-header {
    padding: 10px;
    justify-content: space-between;
    nav{
        justify-self: center;
        align-self: center;
    }
    nav a:not(:last-child) {
        margin-right: 10px;
    }
    nav .active {
        color: $clr1;
    }

    .logo-continer{
        align-items: center;
        &>*{
            font-size: rem(30px);
            text-transform: uppercase;
        }
        .logo{
            letter-spacing: 8px;
            padding-top: 3px;
        }
        svg{
            margin-right: 15px;
            margin-top: 3px;
        }
    }
    
}
