.prodect-continer {
  gap: 60px;
  @media (max-width: 500px) {
    flex-direction: column;
    padding: 15px;
    gap: 0;
    height: auto;
    .prodect-img-continer {
      width: 300px;
      height: 400px;
      margin-left: 15px;
      margin-bottom: 50px;
      img {
        object-fit: contain;
        height: 100%;
      }
    }
    .prodect-info-continer {
      h1 {
        line-height: 2.25rem;
        color: black;
        margin-bottom: 30px;
        font-size: rem(22px);
      }
      button {
        align-self: center;
        width: 170px;
      }
    }

    .prodect-star-continer {
      align-self: center;
    }

    .prodect-description-price-continer {
      p:last-of-type {
        text-align: center;
        font-size: rem(25px);
        margin-top: 30px;
      }
    }
  }
}
.info-continer {
  gap: 60px;
}
.prodect-img-continer {
  width: 100%;
  height: 600px;
  padding: 25px;
  img {
    // object-fit: contain;
    height: 100%;
  }
}

.prodect-info-continer {
  flex-direction: column;
  justify-content: center;
  padding-top: 25px;
  margin: 0;
  @media screen and (max-width: 500px) {
    height: auto;
  }

  h1 {
    text-transform: uppercase;
    margin-bottom: 25px;
  }

  button {
    width: 100px;
    text-transform: uppercase;
    padding: 10px;
    border-radius: 5px;
    background-color: #2371c0;
    transition: 0.4s;
  }
  button:hover {
    background-color: #2371c0a9;
  }
}

.prodect-star-continer {
  margin-bottom: 25px;

  p {
    margin: 0;
    margin-right: 15px;
  }
  align-items: center;
}

.skins-continer {
  border-top: 1px solid gray;
}
